import React, { useState } from "react";
import DatePicker, { registerLocale } from "react-datepicker";
import { Form, Col, Row, Button } from "react-bootstrap";
import "react-datepicker/dist/react-datepicker.css";
import { useAuth0 } from "@auth0/auth0-react";
import { hr } from "date-fns/locale";

const Kalendar = () => {
  registerLocale("hr", hr);
  const [startDate, setStartDate] = useState(new Date());
  return (
    <DatePicker
      wrapperClassName="datePicker"
      selected={startDate}
      onChange={(date) => setStartDate(date)}
      locale="hr"
      dateFormat="dd.MM.yyyy."
      isClearable
      placeholderText="Odaberite datum"
    />
  );
};

const Rezervacija = () => {
  const { user } = useAuth0();
  return (
    <div className="rezervacija">
      <h1>Rezervacija</h1>
      <hr />
      <h4>Odaberite datum dolaska</h4>
      <Kalendar />
      <Form className="mb-5">
        <Form.Group as={Row} controlId="formBasicEmail">
          <Form.Label column sm="12">
            Email address
          </Form.Label>
          <Col sm="4">
            <Form.Control type="email" value={user.email} readOnly />

            <Form.Text className="text-muted">
              Email za rezervaciju je isti kao i kod registracije.
            </Form.Text>
          </Col>
        </Form.Group>
        <Form.Group as={Row} controlId="formBasicBrojGostiju">
          <Form.Label column sm="12">
            Broj gostiju
          </Form.Label>
          <Col sm="4">
            <Form.Control type="text" placeholder="Unesite broj rezervacija" />

            <Form.Text className="text-muted">
              Ovdje upisujete broj rezervacija.
            </Form.Text>
          </Col>
        </Form.Group>
        <Button variant="success" type="submit">
          Pretraži
        </Button>
      </Form>
      <h1 className="text-center">Dostupne rezervacije</h1>
      <hr />
    </div>
  );
};

export default Rezervacija;
