import React from "react";
import { useAuth0 } from "@auth0/auth0-react";
const Profile = () => {
  const { user } = useAuth0();
  return (
    <div>
      <div className="text-center">
        <h1>Prijavljeni ste kao</h1>
        <hr />
        <img src={user.picture} alt="User profile" className="rounded-circle" />
        <h3 className="mt-4">{user.email}</h3>
      </div>
    </div>
  );
};

export default Profile;
