import React from "react";
import Logo from "../images/logo.png";
const Pocetna = () => {
  return (
    <div className="pocetna">
      <div className="text-center">
        <img
          src={Logo}
          className="align-items-center"
          style={{ width: "100px", marginBottom: "30px" }}
          alt="Logo"
        />
      </div>
      <h1 className="text-center">Robinzonski kamp Lučica</h1>
      <hr />
      <h4 className="text-center">Rezervacija</h4>
      <hr />
      <p className="text-justify">
        U kampu Vam nudimo usluge smještaja. Ukoliko ste već iskusni u
        kampiranju dođite s vlastitim šatorom i opremom te iznajmite kamp
        mjesto, a za one malo manje hrabre koji će tek otkriti ovakvu vrstu
        boravka omogućili smo smještaj u drvenim kućicama. Za hranu i vodu se
        brinete sami, a mi smo Vam pripremili mjesta za roštiljanje. Kamp je
        pogodan za sve uzraste, a za goste kampa osiguran je sanitarni čvor.
      </p>
      <br />
      <hr />
      <p className="text-justify">
        Ovo mjesto savršeno je za opuštanje, uživanje i bijeg od užurbane
        svakodnevice. Sami odaberite kako ćete provoditi vrijeme kod nas,
        iskoristite što Vam priroda nudi. Kupajte se u Korani, smaragdnoj
        ljepotici našeg kraja. U blizini kampa nalaze se slapovi, uživajte u
        samom pogledu, sjednite pod slapiće i isprobajte „prirodnu masažu“
        vodom.
      </p>
      <br />
      <hr />
      <p className="text-justify">
        Rijeka Korana izvire kod Plitvičkih jezera (nacionalni park). Njena
        dužina iznosi 134,2 km, a ulijeva se u rijeku Kupu kod Karlovca. Pripada
        crnomorskom slivu i nije plovna. Na mnogim dijelovima Korana protiče
        kroz slikovite, krečnjačke kanjone visoke stotinjak i više metara. Ovi
        kanjoni obrasli su bukovom šumom ili su ogoljeli. U gornjem toku nalaze
        se slikovite pećine.
      </p>
      <hr />
      <p className="text-justify">
        Ispunite svoj dan raznim aktivnostima ili se jednostavno opuštajte i
        ljenčarite u potpuno prirodnom okruženju uz prekrasnu rijeku Koranu.
      </p>
    </div>
  );
};

export default Pocetna;
